    "Parliamentary elections were held in Sri Lanka on 8 and 20 April 2010, to elect 225 members to Sri Lanka's 14th Parliament.\n14,088,500 Sri Lankans were eligible to vote in the election at 11,102 polling stations.\nIt was the first general election to be held in Sri Lanka following the conclusion of the civil war which lasted 26 years. \nThe main parties contesting in the election were the ruling United People's Freedom Alliance (UPFA), the main opposition United National Front (UNF) and the Democratic National Alliance (DNA) led by former commander of the Sri Lankan Army Sarath Fonseka.\nPresident Mahinda Rajapaksa had previously been reelected as president in January 2010. \nAs expected, the UPFA secured a landslide victory in the elections, buoyed by its achievement of ending the 30 year Sri Lankan Civil War and defeating the Liberation Tigers of Tamil Eelam in May 2009.\nThe UPFA won a large majority in the parliament, obtaining 144 seats, an increase of 39 since the 2004 election.\nThe main opposition UNF won 60 seats, a decline of 22.\nThe minority Tamil party Tamil National Alliance (TNA) won 14 seats, down from the 22 they won in 2004, and the DNA, contesting for the first time, won 7 seats.\nThe UPFA however fell short of its goal of obtaining a two-thirds supermajority in the house, which it would have needed to change the constitution on its own.\nThe election had the lowest voter turnout in Sri Lanka since independence. While the elections were initially scheduled to be concluded on 8 April, irregularities in two districts led the Commissioner of Elections to hold re-polls on 20 April.\nFinal results were announced the following day, a day before the new parliament was scheduled to meet for the first time. ",

